            vm.isEmail = false;
        },
        openlocation: function (value) {
      if (value.gin_id != null && value.gin_id !== "undefined") {
        this.ginid = value.gin_id;
      } else {
        this.gin_lineItem_id = value;
      }
      this.GetLocationByCompanyId();
      this.showDeployModal = this.showDeployModal ? false : true;
      this.LocationSelect = $("#hdnCompantLocationSelect").val();
       
        },
        FilterDDLandNormalValue: function (item) {
            if (item && item.length > 0) {
                try {
                    return JSON.parse(item)[0].name;
                } catch (ex) {
                    return item;
                }
            } else {
                return "";
            }
        },
        GetEmailInfo: function (item) {

            this.EmailInfo = item;
            this.sendEmailModal = (this.sendEmailModal) ? false : true;
        },
        openEmailModal: function (item) {
            this.sendEmailModal = true;
            this.modalItem = item;
        },
        getStatusClass: function (status, columnName) {
            var returnClass = ''
            if (columnName == 'status_id' || columnName == 'status_name' || columnName == 'gin_status')
                switch (status) {
                    case 'Active': case 'Approved':
                        returnClass = 'px-2 bg-success text-center text-white d-block';
                        break;
                    case 'Inactive': case 'Closed': case 'Cancelled': case 'Rejected':
                        returnClass = 'px-2 bg-danger text-center text-white d-block';
                        break;
                    case 'Approval Pending': case 'Partialy Approved':
                        returnClass = 'px-2 bg-info text-center text-white d-block';
                        break;
                    case 'Received': case 'Partialy Received':
                        returnClass = 'px-2 bg-secondary text-white text-center d-block';
                        break;
                    case 'Draft':
                        returnClass = 'px-2 bg-warning text-center text-white d-block';
                        break;
                    default:
                        returnClass = 'px-2 bg-light text-center text-dark d-block';
                }
            return returnClass;
        },
        openShowNotePopup: function () {
            var ths = this;
            if (
                typeof ths.checkForAdditionalPermission !== "undefined" &&
                !ths.checkForAdditionalPermission("AddNote")
            ) {
                this.ShowAlert(
                    this.$t("additionalOwnerAuthorityWarning"),
                    "warning",
                    true,
                    this.$t("Alert")
                );
            } else {
                ths.isShowNotePopup = true;
                $("#tab-notes").trigger("click");
                ths.loadData("notes");
            }
        },
        GetLocalizedValue: function (fieldName, displayName) {
            if (this.isBlank(displayName)) {
                var chk = this.$t(String(fieldName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(fieldName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return fieldName;
            }
            else {
                var chk = this.$t(String(displayName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(displayName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return displayName;
            }
        },
        formatDate: function (value) {
            if (value) {
                return moment(value, String(Globalize.culture().calendar.patterns.d).toUpperCase()).format(String(Globalize.culture().calendar.patterns.d).toUpperCase())
            }
        },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },
        changeDateForAddNoteUsrEnd: function (value) {
            try {
                var timeFormat = Globalize.culture().calendar.patterns.t;
                timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
                var getDate = new Date(moment(String(value)).year(), moment(String(value)).month(), moment(String(value)).date(), moment(String(value)).hours(), moment(String(value)).minutes());
                if (userTimeZone[0].indexOf('+') >= 0) {
                    getDate = new Date(getDate.setHours(getDate.getHours() - parseInt(userTimeZone[0].replace("+", ""))));
                    getDate = new Date(getDate.setMinutes(getDate.getMinutes() - parseInt(userTimeZone[1])));
                } else if (userTimeZone[0].indexOf('-') >= 0) {
                    getDate = new Date(getDate.setHours(getDate.getHours() + parseInt(userTimeZone[0].replace("-", ""))));
                    getDate = new Date(getDate.setMinutes(getDate.getMinutes() + parseInt(userTimeZone[1])));
                }
                return moment(getDate, "YYYY-MM-DD HH:mm").format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(timeFormat));
            } catch (e) {
                var timeFormat = Globalize.culture().calendar.patterns.t;
                timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
                return moment(String(value)).format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(timeFormat));
            }
        },
        DeleteActivityData: function (id) {
            var vm = this;
            if (id > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, null, function () {
                    var postJSON = {
                        ids: id,
                        moduleName: "CRM",
                        subModuleCode: vm.$route.params.subModuleCode,
                        refCode: "DELETE_VIEW_ACTIVITY",
                        refId: vm.$route.params.id,
                    };
                    var objectPostString = JSON.stringify(postJSON);

                    LeadDataService.CommonDelete(objectPostString).then(function (
                        response
                    ) {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            vm.ShowAlert(vm.$t("DeletedSuccess", [vm.$t("Activity")]),'success',true,vm.$t('Alert'));
                            vm.loadData("activities");
                        } else {
                            vm.ShowAlert(vm.$t("DeletedError", [vm.$t("Activity")]),'failure',true,vm.$t('Alert'));
                        }
                         
                    });
                });
            }
        },
        openNoteDialogue() {
            this.isShowNoteDialogue = (this.isShowNoteDialogue) ? false : true;
        },
        openAttachmentDialogue() {
            this.isShowAttachmentDialogue = (this.isShowAttachmentDialogue) ? false : true;
        },
        openAddTaskModel() {
            this.showAddTaskModal = (this.showAddTaskModal) ? false : true;
        },
        openAddNewEvent() {
            this.showAddEventModal = (this.showAddEventModal) ? false : true;
        },
        openCallEvent() {
            this.showCallModal = (this.showCallModal) ? false : true;
        },
        openActivityModal(item) {
            if (item.activity_type == "Task") {
                this.showAddTaskModal = true;
                this.modalTaskItem = item;
            }
            else if (item.activity_type == "Follow Up" || item.activity_type == "FollowUp") {
                this.showAddEventModal = true;
                this.modalFollowUpItem = item;
            }
            else if (item.activity_type == "Call") {
                this.showCallModal = true;
                this.modalCallItem = item;
            }
        },
        DeleteActivity(item) {
            this.modalDeleteItem = item;
            this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading=true;
            LeadDataService.FormLayoutView(this.ApiURL).then(function (response) {
                vm.isLoading=false;
                var fullLeftJSON = response.data.LEFTDATA;
                var stausName = $.grep(fullLeftJSON, function (element, index) {
                    return element.name == "status_id";
                });
                if (stausName != null && stausName.length > 0) {
                    vm.stausName = stausName[0];
                }
                fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                    return element.form_field_visibility == "YES";
                });
               
                if (
                    response.data.RIGHTDATA != null &&
                    response.data.RIGHTDATA.length > 0
                ) {
                    var fullJSON = response.data.RIGHTDATA;
                    var gruopBy = "";
                    $.each(
                        fullJSON,
                        $.proxy(function (index, item) {
                            gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                            var chkGroup = $.grep(
                                this.timelinedata,
                                function (element, index) {
                                    return element.groupKey == gruopBy;
                                }
                            );
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullJSON, function (element, index) {
                                    return (
                                        vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy
                                    );
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        groupKey: gruopBy,
                                        groupData: returnedData,
                                    };
                                    vm.timelinedata.push(objGroup);
                                }
                            }
                        }, vm)
                    );
                    
                    vm.noRecordForTab = false;
                } else {
                    vm.noRecordForTab = true;
                }
                if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                    var groupId = fullLeftJSON[0].group_id;
                    $.each(
                        fullLeftJSON,
                        $.proxy(function (index, item) {
                            
                            if (item.name == 'vendor_name') {
                            this.vendorName = item.value;
                                      }
                            var chkGroup = $.grep(
                                this.LeftViewData,
                                function (element, index) {
                                    return element.group_id == groupId;
                                }
                            );
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(
                                    fullLeftJSON,
                                    function (element, index) {
                                        return element.group_id == groupId;
                                    }
                                );
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        LeftViewData: returnedData,
                                    };
                                    vm.LeftViewData.push(objGroup);
                                }
                            }
                            groupId = item.group_id;
                        }, vm)
                    );
                }
               
                setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                setTimeout(function () {vm.RadioButtonBootstrap()}, 500);
            });
        },
        loadData: function (tabName, callFrom) {

            var vm = this;
            vm.DisableButton();
            if (tabName == "notes") {
                vm.isNotes = true;
                vm.activeKey = 2;
                if (callFrom && callFrom == "savenote") vm.isShowNotePopup = false;
            }
            else if (tabName == "email") {
                vm.isEmail = true;
                vm.activeKey = 4;
            }
            else if (tabName == "activities") {
                vm.isDetail = true;
                vm.activeKey = 3;
            }
            if (tabName != "" && tabName != null) {
                if (tabName == "email") {
                    var url =
                        vm.ApiURLEmail +
                        vm.SortBy +
                        "&sortExp=" +
                        vm.SortExp +
                        "&pageSize=" +
                        vm.PageSize +
                        "&pageNumber=" +
                        vm.PageNumber;
                    LeadDataService.EmailListing(url).then(function (response) {
                         
                        if (response.data != null) {
                            vm.EmailData = response.data.data;
                            setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                            setTimeout(function () {vm.setHeaderForSelectedTab()}, 500);
                            setTimeout(function () {vm.bindLeftTabEventForSetHeading()}, 500);
                            vm.noRecordForTab = false;
                        } else {
                            setTimeout(function () {vm.setHeaderForSelectedTab()}, 500);
                            setTimeout(function () {vm.bindLeftTabEventForSetHeading()}, 500);
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "activities") {
                    LeadDataService.ActivityListing(vm.ApiURL).then(function (response) {
                         
                        if (response.data != null && response.data.data.length > 0) {
                            vm.activitiesData = response.data.data;
                            setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                            setTimeout(function () {vm.ResponsiveDataTable('table-openactivities-content')}, 500);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "PO") {
                  var url="subModuleCode="+vm.$route.params.subModuleCode+"&sortBy=" + vm.SortBy + "&sortExp=" + vm.SortExp + "&pageSize=" + vm.PageSize + "&pageNumber=" + vm.PageNumber + "&searchCondition= orders.account_id ="+vm.$route.params.id;
                   LeadDataService.PurchaseOrderListing(url).then(function (response) {
                        if (response.data != null && response.data.data.length > 0) {
                            this.productData = response.data.data;
                            vm.productHeaders = response.data.schema;
                            $.each(vm.productData, function (index, item) {
                                if (item.gin_status == null) {
                                    item.gin_status = '@Resources.NoGin'
                                }
                            });
                            setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                            setTimeout(function () {vm.ResponsiveDataTable('table-products-content')}, 500);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                            vm.productData = null;
                        }
                    });
                    vm.activeKey = 6;
                }
                else if (tabName == "GIN") {
             url = `sourceId=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}&moduleName=${vm.$route.params.moduleName}`;
          LeadDataService.GetGoodInwardForVendor(url).then(function (
            response
          ) {
            vm.ginData = [];
            if (response.data != null && response.data.data.length > 0) {
              var fullJSON = response.data.data;
              var groupBy = "";
              var gin_id = "";
              var gin_status = "";
              $.each(
                fullJSON,
                $.proxy(function (index, item) {
                  gin_id = item.GIN_ID;
                  groupBy = item.GIN_NUMBER;
                  gin_status = item.GIN_STATUS;
                 
    var chkGroup = $.grep(vm.ginData, function (element, index) {
                    return element.gin_id == gin_id;
                  });
                  if (chkGroup.length == 0) {
                    var returnedData = $.grep(
                      fullJSON,
                      function (element, index) {
                        return element.GIN_ID == gin_id;
                      }
                    );
                    if (returnedData.length > 0) {
                      var objGroup = {
                        gin_id: gin_id,
                        gin_status: gin_status,
                        gin_number: groupBy,
                        groupData: returnedData,
                      };
                      vm.ginData.push(objGroup);
                    }
                  }
                }, vm)
              );
              setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
              setTimeout(function () {vm.ResponsiveDataTable('table-GIN-content')}, 500);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
              vm.ginData = null;
            }
          });
            vm.activeKey = 5;
                }
                else if (tabName == "notes") {
                    this.noteData = [];
                    //int sourceId, int companyId, int loggedInUserID, string subModuleCode, string moduleName
                    LeadDataService.NoteListing(vm.ApiURL).then(function (response) {
                         
                        if (response.data != null && response.data.data.length > 0) {
                            vm.noteData = response.data.data;
                            setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "timeline") {
                    url = `id=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}`;
                    LeadDataService.TimelineListing(url).then(function (response) {
                         
                        if (response.data != null) {
                            vm.timelinedata = [];
                            var fullJSON = response.data.data;
                            var gruopBy = "";
                            $.each(fullJSON, $.proxy(function (index, item) {
                                gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                                var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                                    return element.groupKey == gruopBy;
                                });
                                if (chkGroup.length == 0) {
                                    var returnedData = $.grep(fullJSON, function (element, index) {
                                        return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                                    });
                                    if (returnedData.length > 0) {
                                        var objGroup = {
                                            groupKey: gruopBy,
                                            groupData: returnedData
                                        };
                                        vm.timelinedata.push(objGroup);
                                    }
                                }
                            }, vm));
                            setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                    vm.activeKey = 1;
                }
            }
        }
    }
}
</script>